import React from 'react';
import Shl from 'react-syntax-highlighter/prism';
import { coy } from 'react-syntax-highlighter/styles/prism/coy';

import ListItem from '../../../base/listItem';
import PageTitle from '../../../base/pageTitle';
import WordsList from '../../../base/keywordsList';
import eventData from './data.json';

export default function TechEvent(props) {
  const data = eventData;
  const linkListItems = data.linkListItems.slice();
  return (
    <div className="tech tech-event">
      <header>
        <PageTitle title={data.pageTitle} />
        <WordsList words={data.keyWords} />
      </header>
      <article>
        <div className="object">
          <dl>
            <dt>?</dt>
            <dd>
              ,,JavaScript.
            </dd>
            <dd>JavaScript:</dd>
            <dd>
              ,,,,:
            </dd>
            <dd>
              <Shl language="javascript" style={coy}>
                {`
//
//
//: ,,,apply & call,this
//,"":
//1. 
//2. this
//3. , 
//,,.
//,.
//: ,:arguments, this
//arguments
//this,(function context)
//Java,,this.
//JavaScript,4. this,this(invocation context)
//: ,,,
`}
              </Shl>
            </dd>
          </dl>
          <dl>
            <dt>(inline function)</dt>
            <dd>: callback.</dd>
            <dd>
              ,window?window,
            </dd>
            <dd>
              ,,
              
            </dd>
            <dd>
              <Shl language="javascript" style={coy}>
                {`
var ninja = {
  chirp: function(n) {
    return n > 1 ? ninja.chirp(n - 1) + "-chirp" : "chirp";
  }
};
var samurai = { chirp: ninja.chirp };
ninja = {};
//Uncaught TypeError: ninja.chirp is not a function
//ninja.chirp(n - 1),ninja.chirpundefined
console.log(samurai.chirp(3) == "chirp-chirp-chirp")
/*
  //1
  chirp: function (n) {
    //'chirp'
    return n > 1 ? this.chirp(n - 1) + "-chirp" : "chirp";
  }
  //2: , 
  chirp: function signal(n) {
    return n > 1 ? signal(n - 1) + "-chirp" : "chirp";
  }
*/
`}
              </Shl>
            </dd>
          </dl>
          <dl>
            <dt></dt>
            <dd>
              , ,
              , (callback)
            </dd>
            <dd>
              : ,
              .
            </dd>
          </dl>
          <dl>
            <dt></dt>
            <dd className="notice">
              ,!
            </dd>
            <dd>
              :
              ,(),,
            </dd>
            <dd>
              ,
              ,.
              ,
            </dd>
            <dd>
              <Shl language="javascript" style={coy}>
                {`
const store = {
  nextId: 1,
  cache: {},
  add(fn){
    if(!fn.id){
      //''
      fn.id = store.nextId++;
      // true,
      return !!(store.cache[fn.id]=fn);
    }
  }
}
`}
              </Shl>
            </dd>
          </dl>
          <dl>
            <dt>(Self-memoizing)</dt>
            <dd>,,</dd>
            <dd>
              <Shl language="javascript" style={coy}>
                {`
// 
function isPrime(value) {
  if (!isPrime.anwers) isPrime.answers = {};
  if (isPrime.answers[value] != null) {
    return isPrime.answers[value];
  }
  var prime = value != 1; // 1 can never be prime
  for (var i = 2; i < value; i++) {
    if (value % i == 0) {
      prime = false;
      break;
    } 
  }
  return isPrime.answers[value] = prime;
}

//dom
function getElements(name) {
  if(!getElements.cache){
    getElements.cache = {};
  }
  //,
  return getElements.cache[name] = getElements.cache[name] || document.getElementsByTagName(name)
}

//
var elems = {
  length: 0,
  add(elem){
    //or [].push.call(this, elem)
    //push
    //https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/push
    Array.prototype.push.call(this, elem);
  },
  gather(id){
    this.add(document.getElementById(id));
  } 
};
elems.gather("first");
elems.gather("second");
`}
              </Shl>
            </dd>
          </dl>
          <dl>
            <dt></dt>
            <dd>
              <Shl language="javascript" style={coy}>
                {`
//: ,/
function smallest(array){
  return Math.min.apply(Math, array);
}
function largest(array){
  return Math.max.apply(Math, array);
}
function multiMax(multi){
  return multi*Math.max.apply(Math, [].slice.call(arguments,1));
}

/******(overloading)****/

//(,)
//if-else-if
var ninja = {
  whatever: function() {
       switch (arguments.length) {
         case 0:
           /* do something */
           break;
         case 1:
           /* do something else */
           break;
         case 2:
           /* do yet something else */
           break;
       //and so on ...
      } 
  }
}

//,,,
function addMethod(object, name, fn) {
  var old = object[name];
  //,?
  object[name] = function(){
    if (fn.length == arguments.length)
      return fn.apply(this, arguments)
    else if (typeof old == 'function')
      return old.apply(this, arguments);
  };
}
var ninjas = {
  values: ["Dean Edwards", "Sam Stephenson", "Alex Russell"]
};
 addMethod(ninjas, "find", function(){
   return this.values;
});
 addMethod(ninjas, "find", function(name){
   var ret = [];
   for (var i = 0; i < this.values.length; i++)
     if (this.values[i].indexOf(name) == 0)
       ret.push(this.values[i]);
   return ret;
 });
 addMethod(ninjas, "find", function(first, last){
   var ret = [];
   for (var i = 0; i < this.values.length; i++)
     if (this.values[i] == (first + " " + last))
       ret.push(this.values[i]);
   return ret;
 });
`}
              </Shl>
            </dd>
          </dl>
          <dl>
            <dt>(closure)</dt>
            <dd>
              ,,,?
            </dd>
            <dd>,""?</dd>
            <dd>
              <Shl language="javascript" style={coy}>
                {`
function assert(value, desc) {
  const val = value
  let consoleMethod = val ? "log" : "warn";
  let consoleColor = val ? "color:green;" : "color:red;";
  console[consoleMethod]('%c'+desc,consoleColor)
}
var outerValue = 'ninja';
var later;
function outerFunction() {
  var innerValue = 'samurai';
  //,,, 
  //, 
  function innerFunction(paramValue) { 
    assert(outerValue,"Inner can see the ninja.");
    assert(innerValue,"Inner can see the samurai.");
    assert(paramValue,"Inner can see the wakizashi.");
    // true: ,,
    assert(tooLate,"Inner can see the ronin.");
  }
  later = innerFunction;
}
assert(!tooLate,"Outer can't see the ronin."); 
var tooLate = 'ronin';
outerFunction(); 
later('wakizashi');
`}
              </Shl>
            </dd>
            <dd>
              ,,(),
              ,JavaScript
            </dd>
            <dt></dt>
            <dd>
              <Shl language="javascript" style={coy}>
                {`
function Ninja() {
  var feints = 0;
  this.getFeints = function(){
    return feints;
  };
  this.feint = function(){
    feints++;
  }; 
}
var ninja = new Ninja();
ninja.feint();
assert(ninja.getFeints() == 1,"We're able to access the internal feint count.");
assert(ninja.feints === undefined,"And the private data is inaccessible to us.");
`}
              </Shl>
            </dd>
          </dl>
        </div>
        <Shl language="javascript" style={coy} />
        {linkListItems.map((item, index) => (
          <ListItem items={item.items} title={item.title} key={index} />
        ))}
      </article>
    </div>
  );
}



